#content{
    transition: 0.2s;
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    animation: fadein 2s linear;
    background-color: transparent;
    position: relative;
    z-index: 1;
}
#header{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #C2C0C0;
}

#header #header-title{
    font-size: 2.4rem;
    margin-top: 1rem;
}
#header #header-svg{
    margin-top: 2rem;
}

#body{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #C2C0C0;
}
#body #body-title{
    font-size: 2.4rem;
    text-align: center;
}
#body #body-content{
    margin-top: 0.5rem;
    padding: 1.2rem;
    text-align: center;
}
#body #body-link{
    transition: 0.2S;
    font-size: 2.4rem;
    margin: 2.4rem 0;
    text-decoration: none;
    display: block;
    color: white;
    padding: 1.4rem 4rem;
    box-shadow: 0px 0px 3px #1afe49,0px 0px 6px #98F7F1,0px 0px 3px #98F7F1;
}

#body #body-link:hover{
    transition: 0.2s;
    background-color: #12aaa0;
}

#footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    color: #C2C0C0;
}

.media-list{
    list-style-type: none;
    display: flex;
}
.media-list-container{
    list-style-type: none;
    border: none;
    margin: 1rem;
}


@keyframes fadein {
    
    from{
        opacity: 0;
    }
    to{opacity: 1;}

}
