.loader-container{
    background-color: rgba(0, 0, 0, 0.523);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.6rem;
}

.loader{
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #1f2032; /* Blue */
    box-shadow: 0px 0px 3px #1f2032,0px 0px 3px #1f2032,0px 0px 3px #1f2032;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    margin-bottom: 2rem;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }