.navbar{
    background-color: #181623;
    border-bottom: 1px solid #bbbb;
}
.navbar-list{
    display: flex;
    align-items: center;
    justify-content: center;
}
.navbar-list-link{
    display: block;
    font-size: 1.6rem;
    padding: 2rem;
    text-decoration: none;
    color: #C2C0C0;
}

.navbar-list-link:hover{
    transition: 0.2s;
    box-shadow: 0px 0px 3px #1afe49,0px 0px 6px #98F7F1,0px 0px 3px #98F7F1;
    background-color: #177c76;
}

#contact-button{
    transition: 0.2s;
    display: block;
    padding:1rem 4rem;
    margin-right: 1rem;
    border: none;
    background-color: transparent;
    color: white;
    box-shadow: 0px 0px 3px #1afe49,0px 0px 6px #98F7F1,0px 0px 3px #98F7F1;
    cursor: pointer;
}
#contact-button:hover{
    transition: 0.2s;
    background-color: #12aaa0;
}

.empty-space{
    flex: 1;
}