.main-content{
    background-color: #ffff;
    padding: 4rem;
    margin-bottom: 4rem;
    background-color: transparent;
    margin: 1rem;
    color: white;
    box-shadow: 0px 0px 3px #1afe49,0px 0px 3px #98F7F1,0px 0px 3px #98F7F1;
    min-height: 100vh;
    position: relative;
}
.main-content .main-content-header{
    font-size: 3.2rem;
    margin-bottom: 2rem;
    color: #d4cff3;
}



.main-content-title{
    margin: 0.5rem 0;
    font-size: 2.4rem;
    color: #a09bc0;
}
.main-content-paragraph{
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 28px;
    color: rgb(187, 187, 192);
}
.main-content-section{
    margin: 1rem 0 ;
}

.section-img-container{
    margin: 2rem 0;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(300px,auto));
    justify-content: center;
    align-items: center;
    row-gap: 30px;
    column-gap: 100px;
}
.section-img-container img{
    width: 300px; 
    height: 300px;
}

.main-content-list{
    list-style-type: disc;
    margin: 1rem 3rem;
}
.main-content-list-item{
    font-size: 1.4rem;
    margin: 1.4rem 0;
}

.list-box{
    list-style-type: none;
    display: flex;
    margin-bottom: 2rem;

}

.list-box-item-link{
    display: block;
    margin-right: 1rem;
    padding: 1rem 1.4rem;
    font-size: 1.4rem;
    color: white;
    text-decoration: none;
    box-shadow: 0px 0px 3px yellow,0px 0px 3px yellow;
    transition: 0.2s;
}
.list-box-item-link:hover{
    transition: 0.2s;
    background-color: rgb(192, 192, 7);
    color: black;
    font-weight: bold;
}

@media (max-width:900px){
    .list-box{
        flex-direction: column;
    }
    .list-box-item-link{
        margin-bottom: 2rem;
        width: 100%;
    }
}