.modal-container{
    position: fixed;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.94);
    z-index: 100;
}

.modal{
    width: 100%;
    display: flex;
    justify-content: center;
}
.modal form{
    width: 400px;
    padding: 1rem 4rem;
    box-shadow: 0px 0px 3px #1afe49,0px 0px 6px #98F7F1,0px 0px 3px #98F7F1;
    border-top-right-radius: 15%;
    border-top-left-radius: 15%;
    border-bottom-left-radius: 0%;
}

.form-header-box{
    display: flex;
    border-bottom: 1px solid #aaa;
    margin-top: 2rem;
    padding: 1rem 0;
}
.form-header-box img{
    margin-right: 1rem;
}
.form-header{
    font-size: 2.4rem;
    letter-spacing: 1px;
    font-weight: bold;
    flex: 1;
    color: white;
}
.form-header-close-button{
    font-size: 2.4rem;
    letter-spacing: 1px;
    font-weight: bold;
    color: white;
    background-color: transparent;
    cursor: pointer;
}
.input-container{
    display: flex;
    flex-direction: column;
    margin: 2rem 0;
}
.input-container label{
    font-size: 1.6rem;
    font-weight: 450;
    color: rgb(255, 255, 255);

    margin: 1rem 0;
    letter-spacing: 1px
}
.input-container input{
    font-size: 1.6rem;
    background-color: transparent;
    border: none;
    box-shadow: 0px 0px 2px #1afe49,0px 0px 3px #98F7F1,0px 0px 3px #98F7F1;
    padding: 0.8rem 1rem;
    outline: none;
    color: white;
    letter-spacing: 1px;
    border-bottom-left-radius: 1rem;
}
.input-container button{
    margin-top: 2rem;
    font-size: 1.6rem;
    font-weight: 450;
    background-color: transparent;
    border: none;
    color: white;
    width: 100%;
    box-shadow: 0px 0px 3px rgb(255, 225, 0),0px 0px 3px rgb(255, 225, 0);
    letter-spacing: 1px;
    padding: 1.2rem 2rem;
    cursor: pointer;
    transition: 0.3s;
    font-weight: bold;
    border-bottom-left-radius: 1rem;
}
.input-container button:hover{
    transition: 0.3s;
    background-color: rgb(199, 199, 1);
    color: black;
    font-weight: bold;
}