.article-form{
    display: flex;
    flex-direction: column;
}
.article-form button{
    margin-top: 2rem;
    font-size: 1.6rem;
    font-weight: 450;
    background-color: transparent;
    border: none;
    color: white;
    width: 100%;
    box-shadow: 0px 0px 3px rgb(255, 225, 0),0px 0px 3px rgb(255, 225, 0);
    letter-spacing: 1px;
    padding: 1.2rem 2rem;
    cursor: pointer;
    transition: 0.3s;
    font-weight: bold;
    border-bottom-left-radius: 1rem;
    width: 300px;
    position: relative;
}
.form-input-container{
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
}
.form-input-container label{
    font-size: 1.8rem;
    margin: 0.5rem 0;
}
.form-input-container input{
    font-size: 1.6rem;
    background-color: transparent;
    border: none;
    box-shadow: 0px 0px 2px #1afe49,0px 0px 3px #98F7F1,0px 0px 3px #98F7F1;
    padding: 0.8rem 1rem;
    outline: none;
    color: white;
    letter-spacing: 1px;
    border-bottom-left-radius: 1rem;
    
}
.form-input-container textarea{
    font-size: 1.6rem;
    background-color: transparent;
    border: none;
    box-shadow: 0px 0px 2px #1afe49,0px 0px 3px #98F7F1,0px 0px 3px #98F7F1;
    min-height: 20vh;
    color: white;
    padding: 0.5rem;
    resize: none;
}




.article-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(300px,auto));
    justify-content: center;
    margin: 0 auto;
    column-gap: 100px;
    row-gap: 30px;
}
.article{
    border-radius: 0.4rem;
    border: 1px solid #aaaa;
    width: 300px;
    position: relative;
    display: flex;
    flex-direction: column;
}
.article-text-container{
    margin: 1rem;
    flex: 1;
}
.article .article-title{
    font-size: 1.6rem;
    overflow: hidden;
    text-overflow: ellipsis;
    width:100%
}
.article .article-autor{
    font-size: 1.2rem;
    margin-top: 0.5rem;
}
.article img{
    width: 300px;
    height: 300px;
}
.article-button-container{
    padding: 1rem;
}
#show-article-container{
    display: flex;
    justify-content: center;
    position: relative;
}
.show-article{
    margin-bottom: 1rem;
    font-size: 1.6rem;
    font-weight: 450;
    background-color: transparent;
    border: none;
    color: white;
    width: 100%;
    box-shadow: 0px 0px 3px rgb(255, 225, 0),0px 0px 3px rgb(255, 225, 0);
    letter-spacing: 1px;
    padding: 1.2rem 2rem;
    cursor: pointer;
    transition: 0.3s;
    font-weight: bold;
    border-bottom-left-radius: 1rem;
    width: 100%;
}
#button-open-form{
    width: 300px;
    margin: 2rem 0;
    margin-bottom: 4rem;
    position: inherit;
}

.show-article:hover{
    background-color: rgb(216, 216, 35);
    color: black;
    font-weight: bold;
}


.article-container-main img{
    width: 50%;

}
.article-container-main h1{
    margin: 0.5rem 0;
    font-size: 2.4rem;
    color: #a09bc0;
    border-bottom: 1px solid #aaa;
    padding: 1rem 0;
}

.article-container-main pre{
    font-size: 1.6rem;
    background-color: transparent;
    border: none;
    min-height: 20vh;
    color: white;
    padding: 0.5rem;
    resize: none;
    white-space: pre-wrap;
    word-wrap: break-word;
}
.article-autor{
    font-size: 1.8rem;
    margin: 2rem 0;
    padding: 1rem 0;
    border-bottom: 1px solid #aaa;
}
.img-container{
    text-align: center;
    margin: 4rem 0;
}