.box{
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
}
.box-content{
    margin-bottom: 6rem;
    box-shadow: 0px 0px 3px #1afe49,0px 0px 3px #98F7F1,0px 0px 4px #98F7F1;
    background-color: rgb(26, 30, 44);
    padding: 2rem;
}
.box-image{
    display: flex;
    margin: 0.5rem 0;
}
.box-image h1{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1.4rem;
    font-weight: bold;
    font-size: 2.4rem;
    color: #a09bc0;
}
.box-image img{
    width: 64px;
    height: 64px;
    border-radius: 50%;
}
.box-text{
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 28px;
    color: rgb(187, 187, 192);
    margin-top: 2rem;
}